<template>
  <div class="AccountsReceivableStatistics pa-3">
    <div v-if="checkAllDataReady">
      <div class="d-flex justify-space-between align-center">
        <h1>應收帳款統計表 ({{ list.startDay }} ~ {{ list.endDay }})</h1>
        <v-spacer></v-spacer>
        <v-text-field
          type="month"
          label="選擇月份"
          v-model="selectMonth"
        ></v-text-field>
        <v-btn class="primary ml-1" @click.stop="print()">
          列印應收帳款一覽表
          <v-icon class="ml-1">mdi-printer</v-icon>
        </v-btn>
      </div>

      <v-divider class="my-2"></v-divider>

      <v-simple-table fixed-header>
        <template v-slot:default>
          <thead class="text-no-wrap">
            <tr class="grey lighten-4">
              <th class="text-center text-h6 font-weight-bold">發出請款單</th>
              <th class="text-center text-h6 font-weight-bold">已收款</th>
              <th class="text-center text-h6 font-weight-bold d-print-none">
                <v-icon @click.stop="printCustomerList(filterShipment)"
                  >mdi-printer</v-icon
                >
                對帳單
              </th>
              <th class="text-center text-h6 font-weight-bold">客戶名稱</th>
              <th class="text-center text-h6 font-weight-bold">買賣金額</th>
              <th class="text-center text-h6 font-weight-bold">沖鎖金額</th>
              <th class="text-center text-h6 font-weight-bold">折工金額</th>
              <th class="text-center text-h6 font-weight-bold">加工金額</th>
              <th class="text-center text-h6 font-weight-bold">出貨總金額</th>
            </tr>
          </thead>
          <tbody class="text-right">
            <tr v-for="(item, index) in filterShipment" :key="index">
              <td>
                <div class="d-flex align-center text-no-wrap">
                  <v-checkbox
                    v-model="item.paymentState.sendShipment"
                    @click.stop="changeState(item, 'sendShipment')"
                  ></v-checkbox>
                  {{ item.paymentState.sendShipmentDate }}
                </div>
              </td>
              <td>
                <div class="d-flex align-center text-no-wrap">
                  <v-checkbox
                    v-model="item.paymentState.getPayment"
                    @click.stop="changeState(item, 'getPayment')"
                  ></v-checkbox>
                  {{ item.paymentState.getPaymentDate }}
                </div>
              </td>
              <td class="text-center text-h6 d-print-none">
                <v-icon @click.stop="printCustomerListSingle(item)"
                  >mdi-printer</v-icon
                >
              </td>
              <td class="text-center text-h6">{{ item.name }}</td>
              <td class="text-center text-h6">{{ item.allTotalPrice }}</td>
              <td class="text-center text-h6">{{ item.allPunchlockPrice }}</td>
              <td class="text-center text-h6">{{ item.allFoldPrice }}</td>
              <td class="text-center text-h6">{{ item.allMachiningPrice }}</td>

              <td class="text-center text-h6">
                {{
                  item.allTotalPrice +
                  item.allPunchlockPrice +
                  item.allFoldPrice +
                  item.allMachiningPrice
                }}
              </td>
            </tr>

          </tbody>
        </template>
      </v-simple-table>
    </div>
    <div v-else>
      <h2>資料處理中...</h2>
    </div>

  </div>
</template>

<script>
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

export default {
  data() {
    return {
      shipmentItems: null, //對帳單資料
      paymentItems: null, //應收帳款資料
      selectMonth: "",
      list: {
        startDay: null,
        endDay: null,
      },
      cData: JSON.parse(sessionStorage.getItem("cData")),
      // customerItems: [], //暫存所有客戶名單
    };
  },

  filters: {
    filtermoney(num) {
      if (!num) {
        return "";
      }
      const n = Number(num);
      return `$${n.toFixed(0).replace(/./g, (c, i, a) => {
        const currency =
          i && c !== "." && (a.length - i) % 3 === 0
            ? `, ${c}`.replace(/\s/g, "")
            : c;
        return currency;
      })}`;
    },
  },

  watch: {
    selectMonth(newVal) {
      this.list.startDay = dayjs(newVal).date(1).format("YYYY-MM-DD");
      let theMonthLastDay = dayjs(newVal).daysInMonth();
      this.list.endDay = dayjs(newVal).format("YYYY-MM-" + theMonthLastDay);
      this.paymentItems = null;
      this.getData("payment");
    },
  },

  computed: {
    //判斷需要的資料是否都已讀入
    checkAllDataReady() {
      if (this.shipmentItems == null || this.paymentItems == null) {
        return false;
      } else {
        return true;
      }
    },
    //建構列表表格內容
    filterShipment() {
      //1.取得.符合時間區段的資料
      let match = this.shipmentItems.filter((i) =>
        dayjs(i.date).isBetween(
          this.list.startDay,
          this.list.endDay,
          null,
          "[]"
        )
      );
      //計算出1.的資料中的各項資料總合
      match = match.map((i) => {
        return {
          ...i,
          totalPrice: this.priceTotal(i), //買賣金額
          punchlockPrice: this.optionTotal(
            i,
            "punchlock",
            "extraPunchlockPrice"
          ), //沖鎖
          foldPrice: this.optionTotal(i, "fold", "extraFoldPrice"), //折工
          machiningPrice: this.optionTotal(
            i,
            "machining",
            "extraMachiningPrice"
          ), //加工
        };
      });

      //2.取得所有符合資料的顧客snkey -> 取得不重覆陣列值
      let snkeyItems = match.map((i) => i.customer.snkey); //取得所有符合資料的顧客snkey
      snkeyItems = [...new Set(snkeyItems)]; //取得不重覆陣列值
      let createTableDatas = snkeyItems.map((snkey) => {
        let matchArr = match.filter((i) => i.customer.snkey == snkey);

        return {
          code: matchArr[0].customer.code,
          name: matchArr[0].customer.name,
          allTotalPrice: this.comTotal(matchArr, "totalPrice"),
          allPunchlockPrice: this.comTotal(matchArr, "punchlockPrice"),
          allFoldPrice: this.comTotal(matchArr, "foldPrice"),
          allMachiningPrice: this.comTotal(matchArr, "machiningPrice"),
          data: matchArr,
          //加入應收帳款確認狀態 -> 傳入 (顧客snkkey,月份) -> 判斷資料是否已紀錄 1.已發出請款單 2.已收款 的日期
          paymentState: this.returnPaymentState(
            matchArr[0].customer.snkey,
            this.selectMonth
          ),
        };
      });
      return createTableDatas;
    },
    //應收帳款資料
    // paymentItems() {
    //   return this.$store.state.paymentItems;
    // },
  },

  mounted() {
    this.getData("shipment");
    this.getData("payment");
    // this.$store.dispatch("getItems", "payment"); //取得所有應收帳款資料
    this.selectMonth = dayjs().format("YYYY-MM"); //用這個去觸發watch的反應
  },

  methods: {
    changeState(item, type) {
      if (item.paymentState[type]) {
        item.paymentState[type + "Date"] = dayjs().format("YYYY-MM-DD");
      } else {
        item.paymentState[type + "Date"] = "";
      }
      let postData = {
        snkey: item.paymentState.snkey,
        datalist: JSON.stringify(item.paymentState),
      };

      this.$api.post("/payment",postData)
      .then((rs) => {
        console.log(rs);
      });
    },
    //回傳應收帳款資料
    returnPaymentState(customerSnkey, selectMonth) {
      let match = this.paymentItems.find(
        (i) => i.customerSnkey == customerSnkey && i.selectMonth == selectMonth
      );
      if (match) {
        return match;
      } else {
        let list = {
          customerSnkey: customerSnkey,
          selectMonth: selectMonth,
          sendShipment: false,
          sendShipmentDate: "",
          getPayment: false,
          getPaymentDate: "",
          create_man:
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")", //執行動作的人,時間,內容
        };
        let postData = {
          personnel_snkey: this.$store.state.pData.snkey,
          datalist: JSON.stringify(list),
        };
        this.myAxios(
          "general/add/" + this.$store.state.databaseName + "/payment",
          postData
        ).then((rs) => {
          console.log(rs);
        });
        return list;
      }
    },
    //取得所有應收帳款資料
    getData(dataTable) {
      this.$api.get(dataTable)
      .then((rs) => {
        this[dataTable + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //回傳單筆資料總額
    priceTotal(items) {
      return items.tempItems.reduce((total, item) => {
        return (total += this.filterFinalPrice(item));
      }, 0);
    },
    filterFinalPrice(item) {
      if (item.noSize) {
        var val =
          item.quantity *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      } else {
        var val =
          item.weight *
          (parseFloat(item.rawmaterial.price) +
            parseFloat(item.extraRawmaterialPrice));
      }
      return Math.round(val); //2021.3.19 改回傳四捨五入,取整數
      // return this.roundUp(val, 0);
    },
    //各種工作總金額
    optionTotal(items, type, extraType) {
      return items.tempItems.reduce((total, item) => {
        if (item[type]) {
          return (total +=
            (parseFloat(item[type].price) + parseFloat(item[extraType])) *
            item.quantity);
        } else {
          return total;
        }
      }, 0);
    },
    //回傳指定項目的總合
    comTotal(items, type) {
      return items.reduce((total, i) => {
        return (total += parseInt(i[type]));
      }, 0);
    },
    //列印指定客戶應收帳款列表
    printCustomerList(items) {
      let data = {
        items: items,
        dates: this.list,
      };
      let routeData = this.$router.resolve({
        path: "/print/customershipmentlistprint",
        query: { data: JSON.stringify(data) },
      });
      window.open(routeData.href, "_blank");
    },
    //列印指定客戶應收帳款列表(單筆)
    printCustomerListSingle(item) {
      let singleItem = [];
      singleItem[0] = item;
      let data = {
        items: singleItem,
        dates: this.list,
      };
      let routeData = this.$router.resolve({
        path: "/print/customershipmentlistprint",
        query: { data: JSON.stringify(data) },
      });
      window.open(routeData.href, "_blank");
    },
    //列印應收帳款統計一覽表
    print() {
      let routeData = this.$router.resolve({
        path: "/print/shipmentlistprint",
        query: { data: JSON.stringify(this.list) },
      });
      window.open(routeData.href, "_blank");
    },
  },
};
</script>

<style>
@media print {
  @page {
    size: A4 portrait;
    margin: 0cm;
    padding: 0cm;
  }
}
</style>